import Drawable from "./Drawable";

export default class Rectangle extends Drawable {
  constructor(width = 0, height = 0) {
    super();
    this.lineStyle = 'o';
    this.fillStyle = '/';
    this.width = width;
    this.height = height;
  }
  set width(val) {
    this._width = Math.round(val);
  }
  set height(val) {
    this._height = Math.round(val);
  }
  get height() {
    return this._height;
  }
  get width() {
    return this._width;
  }
  compute() {

    this._rawData = new Array(this.height).fill([]).map((element, idx) => {
      if (idx == 0 || idx == this._rawData.length - 1) {

        element = new Array(this._width).fill(this.lineStyle);
      } else {
        element = new Array(this._width - 2).fill(this.fillStyle);
        element.push(this.lineStyle);
        element.unshift(this.lineStyle);
      }
      return element;
    });
  }
}


