

const availableCodePool = '1234567890+)(*/.;^%$#@!_={}[]:,ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';


function randomNum(max, min) {
  return Math.floor(Math.random() * (max - min + 1) + min)
}
export default {
  getRandomChar() {
    return availableCodePool.charAt(randomNum(0, availableCodePool.length));
  }
}


