export default class Drawable {

  constructor() {
    this._x = 0;
    this._y = 0;
    this._width=0;
    this._height=0;
    this._rawData = [];
  }
  compute () {
    
  }
  get rawData() {
    return this._rawData;
  }
  set rawData(val) {
    this._rawData= val;
  }
  get x() {
    return this._x;
  }
  set x(val) {
    this._x = Math.round(val);
  }

  get y() {
    return this._y;
  }
  set y(val) {
    this._y = Math.round(val);
  }

  get height() {
    return this.rawData.length;
  }
  get width() {
    if (this.rawData.length > 0) {
      return this.rawData[0].length;
    } else {
      return 0;
    }
  }
}


