export default class Stage {
  static renderPool= [];
  constructor(w,h,el) {

    this.width=w;

    this.height = h;

    this.rawData = new Array(h).fill(new Array(w).fill(' '));  

    this.element = document.getElementById(el);

  }
  addChild(t) {
    Stage.renderPool.push(t);
  }
  removeChild(t) {

  }

  render () {
    var poolLength = Stage.renderPool.length;
    this.fillWith(' ') ;

    for (var i=0,target;i<poolLength;i++) {
      target = Stage.renderPool[i];
      target.compute();
      var dataLength = target.rawData.length;
      for (var k=0;k<dataLength;k++) {
        
        if (target.y+k>=this.height) {
          return;
        }
        this.rawData[target.y+k] = this.rawData[target.y+k].slice(0,target.x).concat(target.rawData[k],this.rawData[target.y+k].slice(target.x+target.width));
      }
    }


    let linear = this.rawData.map(item=>{
      return item.join('');
    })
    this.element.innerHTML = linear.join(String.fromCharCode(10));
  }
  fillWith(char) {
    this.rawData = new Array(this.height).fill(new Array(this.width).fill(char));  
  }
  clear() {   
    this.rawData = new Array(this.height).fill(new Array(this.width).fill(" "));  
  }
}



